.wrapper {
  position: relative;

  display: flex;
  flex-direction: column;
  min-height: 100vh;
  // stylelint-disable-next-line
  min-height: calc(100 * var(--vh, 1vh));
  overflow: hidden;

  .header,
  .footer {
    flex-shrink: 0;
  }

  main {
    flex-grow: 1;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.overlay {
  display: none;
  position: fixed;
  z-index: 6;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  background-color: $color-overlay;

  &[data-is-overlay="true"] {
    display: block;
  }
}

