@mixin retina {
  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    @content;
  }
}

// Desktop first

@mixin vp-1023 {
  @media (max-width: $vp-1024 - 1) {
    @content;
  }
}

@mixin vp-767 {
  @media (max-width: $vp-768 - 1) {
    @content;
  }
}

// ---------------------------------

//        ,    
//     ,       


//    :

// 1366      
// 1365  1024   
@mixin fluid-desktop {
  @media (min-width: $vp-1024) and (max-width: $vp-1366 - 1) {
    @content;
  }
}

// 769  1023   
@mixin fluid-tablet {
  @media (min-width: $vp-768 + 1) and (max-width: $vp-1024 - 1) {
    @content;
  }
}

// 321  767   
@mixin fluid-mobile {
  @media (min-width: $vp-320 + 1) and (max-width: $vp-768 - 1) {
    @content;
  }
}
